<template>
  <div class="bookshelf">
    <van-nav-bar title="书架" class="van-head">
      <template #left>
        <div class="fl title-logo"></div>
      </template>
      <template #right>
        <van-icon name="wap-home" size="20" @click="toggleTabBar('Bookmall')"></van-icon>
      </template>
    </van-nav-bar>

    <div class="bookshelf-box">
      <div class="clearfix bookshelf-box-t" :class="isLeft ? 'left':'right'">
        <div class="bookshelf-box-title fl" :class="isActive ? 'active':''" @click="my">我的书架</div>
        <div class="fl">|</div>
        <div class="bookshelf-box-title fr" :class="!isActive ? 'active':''" @click="history">阅读记录</div>
      </div>
      <!-- 书架 -->
      <div class="bookshelf-books" v-show="isActive">
        <div
          class="bookshelf-books-list clearfix"
          v-for="(item,index) in getbooksData"
          :key="index"
          @click="skidId(item._id)"
        >
          <div class="bookshelf-books-img fl">
            <img class="auto-img" :src="item.cover" alt />
          </div>
          <div class="bookshelf-books-box fl">
            <div class="bookshelf-books-title">{{item.title}}</div>
            <div class="bookshelf-books-content">作者：{{item.author}}</div>
            <div class="bookshelf-books-content">最近更新时间{{item.updated}}</div>
          </div>
          <div class="book-icons fr">
            <van-icon name="delete" size="20" @click="deleteBook(item._id)" @click.capture.stop />
          </div>
        </div>
      </div>
      <!-- 阅读记录 -->
      <div class="bookshelf-books" v-show="!isActive">
        <div
          class="bookshelf-books-list clearfix"
          v-for="(item,index) in CatalogueData"
          :key="index"
          @click="toHistory(item.bookId,item.link,item.chapterIndex)"
        >
          <div class="bookshelf-books-img fl">
            <img class="auto-img" :src="item.img" alt />
          </div>
          <div class="bookshelf-books-box fl">
            <div class="bookshelf-books-title">{{item.title}}</div>
            <div class="bookshelf-books-content">作者：{{item.author}}</div>
            <div class="bookshelf-books-content">最近阅读至 {{item.chapterTitle}}</div>
          </div>
          <div class="book-icons fr">
            <van-icon
              name="delete"
              size="20"
              @click="deleteHistory(item.bookId)"
              @click.capture.stop
            />
          </div>
        </div>
      </div>
    </div>
    <div class="bookshelf-going" @click="toggleTabBar('Bookmall')">去书城随便逛逛</div>
  </div>
</template>

<script>
import "../../assets/css/bookshelf.less";
export default {
  data() {
    return {
      isActive: true,
      // 初始数据
      getbooksData: [],
      // 我的书架数据
      bookshelfData: [],
      // 阅读历史
      historyData: [],
      isLeft: true,
      // 章节
      CatalogueData: [],
      //获取所有章节
      CatalogueAllData: [],
      // 章节link
      urls: "",
      catalId: "",
      idLink: "",
      book: true,
    };
  },
  created() {
    this.setBookshelfData();
    this.historyData = this.$store.state.readHistory;
  },
  methods: {
    //返回
    back() {
      this.$router.go(-1);
    },
    //路由跳转
    toggleTabBar(name) {
      this.$router.push({ name: name });
    },
    my() {
      if (!this.isActive) {
        this.isActive = true;
        this.isLeft = true;
      }
    },
    history() {
      if (this.isActive) {
        this.isActive = false;
        this.isLeft = false;
        this.CatalogueData = [];
        this.setbookData();
      }
    },
    //我的书架跳转阅读页
    skidId(id) {
      for (let i = 0; i < this.historyData.length; i++) {
        if (this.historyData[i].bookId == id) {
          this.book = false;
          this.$router.push({
            name: "Readnow",
            query: {
              id: id,
              url: this.historyData[i].chapterId,
              index: this.historyData[i].index,
            },
          });
          return;
        }
      }
      if (this.book) {
      this.getBookLink(id);

      }
    },
    // 获取书架内容书籍
    getDetailBookData(id, text, url, index) {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/book-info/" + id,
      })
        .then((result) => {
          this.$toast.clear();
          let data = "";
          if (result.data) {
            data = result.data;
          }
          //图片
          data.cover = unescape(result.data.cover).slice(7);
          data.updated = data.updated.substr(0, 10);
          //存储书籍数据
          if (this.isActive) {
            this.bookshelfData.push(data);
          } else {
            let obj = {
              img: data.cover,
              author: data.author,
              title: data.title,
              chapterTitle: text,
              bookId: data._id,
              link: url,
              chapterIndex: index,
            };
            this.CatalogueData.push(obj);
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    //获取书籍目录
    getBookLink(id) {
      this.axios({
        method: "GET",
        url: "http://novel.kele8.cn/book-sources?view=summary&book=" + id,
      })
        .then((result) => {
          if (result.status == 200) {
            this.urls = result.data[0]._id;
          }

          this.axios({
            method: "GET",
            url: "http://novel.kele8.cn/book-chapters/" + this.urls,
          })
            .then((result) => {
              if (result.status == 200) {
                this.CatalogueAllData = result.data.chapters;
                this.idLink = this.CatalogueAllData[0].link;
                this.$router.push({
                  name: "Readnow",
                  query: {
                    id: this.CatalogueAllData[0]._id,
                    url: this.idLink,
                    index: 0,
                  },
                });
              }
            })
            .catch((err) => {});
        })
        .catch((err) => {});
    },
    // 遍历书架书籍
    setBookshelfData() {
      // 把序列化成字符串的数据反序列化为数组
      // localStorage.getItem("BSID"):获取本地存储BSID
      let ids = JSON.parse(localStorage.getItem("BSID"));

      //遍历数组
      if(ids == null){
        return;
      }
      ids.map((v) => {
        this.getDetailBookData(v);
      });
      //赋值
      this.getbooksData = this.bookshelfData;
    },

    //删除书架书籍
    deleteBook(id) {
      let ids = JSON.parse(localStorage.getItem("BSID"));
      for (let i = 0; i < ids.length; i++) {
        if (ids[i] == id) {
          ids.splice(i, 1);
        }
      }
      this.bookshelfData = [];
      if (ids == "") {
        this.isnull = true;
      }
      localStorage.setItem("BSID", JSON.stringify(ids));
      this.setBookshelfData();
    },
    //最近阅读
    setbookData() {
      if(this.historyData == null){
        return;
      }
      this.historyData.map((v) => {
        this.getDetailBookData(v.bookId, v.chapterTitle, v.chapterId, v.index);
      });
    },
    //删除最近阅读书籍
    deleteHistory(item) {
      for (let i = 0; i < this.historyData.length; i++) {
        if (this.historyData[i].bookId == item) {
          this.historyData.splice(i, 1);
          this.CatalogueData = [];
          this.setbookData();
          return;
        }
      }
    },

    //跳转阅读
    toHistory(id, url, index) {
      this.$router.push({
        name: "Readnow",
        query: {
          url: url,
          id: id,
          index: index,
        },
      });
    },
  },
};
</script>